import { MenuBase } from './bases/menu-base';

/**
 * Cre une instance de menu de la sidebar hrit de {MenuBase}.
 * @class
 * @param {{icone: string}} menu - Les donnes du menu.
 * @returns {SidebarMenuModel} Une instance de SidebarMenuModel.
 */
export class SidebarMenuModel extends MenuBase {
  /**
   * Cre une instance de SidebarMenuModel.
   * @constructor
   * @param {{icone: string}} menu - Les donnes du menu.
   */
  constructor(menu) {
    super(menu);
    const { icone } = menu;
    this._icone = icone;
  }

  /**
   * @return {string}
   */
  get iconeSrc() {
    return `/sport-see/assets/svg/${this._icone}.svg`;
  }

  /**
   * @return {string}
   */
  get iconeAlt() {
    return `Icone ${this._icone}`;
  }
}
