import '../utils/utils';
import { MenuBase } from './bases/menu-base';

/**
 * Cre une instance de menu de la sidebar hrit de {MenuBase}.
 * @class
 * @param {{label: string}} menu - Les donnes du menu.
 * @returns {ToolbarMenuModel} Une instance de ToolbarMenuModel.
 */
export class ToolbarMenuModel extends MenuBase {
  /**
   * Cre une instance de ToolbarMenuModel.
   * @constructor
   * @param {{label: string}} menu - Les donnes du menu.
   */
  constructor(menu) {
    super(menu);
    const { label } = menu;
    this._label = label;
  }

  /**
   * @return {string}
   */
  get label() {
    return this._label.ucFirst();
  }
}
